import { bindActionCreators } from "redux";
import { RootState } from "../../../store/reducer";
import { connect, ConnectedProps } from 'react-redux';
import { getTaskTypes, deleteTaskType, setLoadingState } from '../../../store/tasktypes/actionsCreator';

const mapStateToProps = (state: RootState) => ( {
    taskTypesState : state.taskTypesReducer
})
  
const mapDispatchToProps = (dispatch: any) =>
      bindActionCreators({ getTaskTypes, deleteTaskType, setLoadingState }, dispatch);
  
export const taskTypesListConnector = connect(mapStateToProps, mapDispatchToProps);
  
export type TaskTypeListProps = ConnectedProps<typeof taskTypesListConnector>;