import "mapbox-gl/dist/mapbox-gl.css";
import "react-map-gl-geocoder/dist/mapbox-gl-geocoder.css";
import React, { Component } from "react";
import MapGL from "react-map-gl";
import DeckGL, { GeoJsonLayer } from "deck.gl";
import Geocoder from "react-map-gl-geocoder";

const MAPBOX_TOKEN =
  "sk.eyJ1IjoicmljYXJkb3J1aWh1Z28iLCJhIjoiY2tuMXZ3ZWh1MHk3NjJ3cXV3OHljMGhnMiJ9.PZHyIVphSzOHc6R9xqrJBw";


class MapView extends Component {   

     
    constructor(props) {
      super(props);
      this.onLocationSelected = props.onLocationSelected;
    }
    state = {
      viewport: {
        width: 752,
        height: 450,
        latitude: 37.7577,
        longitude: -122.4376,
        zoom: 8
      },
      searchResultLayer: null
    };
  
    mapRef = React.createRef();
    geocoderContainerRef = React.createRef();
  
    componentDidMount() {
      window.addEventListener("resize", this.resize);
      this.resize();
    }


  
    componentWillUnmount() {
      window.removeEventListener("resize", this.resize);
    }
  
    resize = () => {
      this.handleViewportChange({
        width: 752,//window.innerWidth,
        height: 450//window.innerHeight
      });
     
    };
  
    handleViewportChange = viewport => {
      this.setState({
        viewport: { ...this.state.viewport, ...viewport }
      });
    };
  
    // if you are happy with Geocoder default settings, you can just use handleViewportChange directly
    handleGeocoderViewportChange = viewport => {
      const geocoderDefaultOverrides = { transitionDuration: 1000 };
  
      return this.handleViewportChange({
        ...viewport,
        ...geocoderDefaultOverrides
      });
    };
  
    createResult = (eventResult) => {
      var result = {};
      if (eventResult.address) {
        result.door = eventResult.address;
      }

      eventResult.context.forEach(element => {
        if (element.id.startsWith("postcode")) {
          result.zipCode = element.text;
        }
        if (element.id.startsWith("locality")) {
          result.locality = element.text;
        }
        if (element.id.startsWith("region")) {
          result.city = element.text;
        }
        if (element.id.startsWith("country")) {
          result.country = element.text;
        }
      });

      result.alias = eventResult.place_name;
      result.street = eventResult.text;
      result.longitude = eventResult.geometry.coordinates[0];
      result.latitude = eventResult.geometry.coordinates[1];
      result.id = 0;


      return result;
    }
    handleOnResult = event => {
      this.onLocationSelected(this.createResult(event.result));
        console.log(event.result);
      this.setState({
        searchResultLayer: new GeoJsonLayer({
          id: "search-result",
          data: event.result.geometry,
          getFillColor: [255, 0, 0, 128],
          getRadius: 1000,
          pointRadiusMinPixels: 10,
          pointRadiusMaxPixels: 10
        })
      });
    };
  
    render() {
      const { viewport, searchResultLayer } = this.state;
      
      return (
        <div>
          <div
            ref={this.geocoderContainerRef}
            style={{
              height: 50,
              background: "black",
              display: "flex",
              alignItems: "center",
              paddingLeft: 6,
              paddingRight: 6
            }}
          />
          <MapGL
            ref={this.mapRef}
            {...viewport}
            onViewportChange={this.handleViewportChange}
            mapboxApiAccessToken={MAPBOX_TOKEN}
          >
            <Geocoder
              mapRef={this.mapRef}
              containerRef={this.geocoderContainerRef}
              onResult={this.handleOnResult}
              onViewportChange={this.handleGeocoderViewportChange}
              mapboxApiAccessToken={MAPBOX_TOKEN}
            />
            <DeckGL {...viewport} layers={[searchResultLayer]} />
          </MapGL>
        </div>
      );
    }
  }
  
  export default MapView;