import { getTasks, deleteTask, setLoadingState } from '../../../store/tasks/actionsCreator';
import { connect, ConnectedProps } from 'react-redux';
import { bindActionCreators} from 'redux';
import { RootState } from '../../../store/reducer';

const mapStateToProps = (state: RootState) => ( {
    taskState : state.tasksReducer
})

const mapDispatchToProps = (dispatch: any) => bindActionCreators({ getTasks , deleteTask, setLoadingState }, dispatch);
  
export const taskListConnector = connect(mapStateToProps, mapDispatchToProps);
  
export type TaskListProps = ConnectedProps<typeof taskListConnector>;