import { Button, Col, Form, FormInstance, Input, InputNumber, PageHeader, Row, TimePicker } from 'antd';
import axios from 'axios';
import React from 'react';
import { withRouter } from 'react-router-dom';
import { ITaskType } from '../../store/tasktypes/actionTypes';
import { globalSettings } from '../../global';
import moment from 'moment';
import { taskTypeconnector, TaskTypeRouteComponentPros, TaskTypesProps, ITaskTypeModel } from '../props/tasktypes/index';
import { showError, showOperationSuccess } from './helpers';

type TaskTypeState = {
    taskType: ITaskTypeModel
}

const initialTaskTypeModel : ITaskTypeModel = {
    cost :0,
    name:'',
    duration :600,
    id: -1,
    key : -1,
    momentDuration : moment({minutes : 10})
};

class TaskType extends React.Component<TaskTypesProps & TaskTypeRouteComponentPros, TaskTypeState> {

    formRef = React.createRef<FormInstance>();

    constructor(props : TaskTypesProps & TaskTypeRouteComponentPros){
        super(props);
        this.state = {
            taskType: initialTaskTypeModel,
        }
    }

    componentDidMount() {   
        if(this.props.match.params.id !== undefined) {
            this.getTaskType(this.props.match.params.id);
        }
    }

    // On edit mode get the task type
    async getTaskType(id: string)  {
        this.props.setLoadingState(true);
        
        axios.get(`${globalSettings.ApiAdress}tasktypes/${id}`)
        .then(({data}) =>  {
            this.props.setLoadingState(false);
            this.onTaskTypeFetched(data as ITaskType);
        })
        .catch((err: Error) => {
            this.props.setLoadingState(false);
            showError('Error loading task type.', err.message)
        });
    } 

    onTaskTypeFetched (data:ITaskType) {
        const date: Date = new Date(data.duration * 1000);
        const momentDuration = moment(date).utc();
        this.setState({
            taskType :  {...data, momentDuration} 
        })
        //must be done because of initial values of form that aren't updated.
        this.formRef.current?.resetFields();
    }

    onFinish = (values: any) => {
        const task : ITaskType = {
            key: this.state.taskType.key,
            id: this.state.taskType.id,
            cost: values.cost,
            duration : ((values.duration.hours() * 60) + values.duration.minutes()) * 60,
            name : values.name,
        }

        this.props.upsertTaskType(task, 
            (err: Error) =>{
                const errorMessage = (this.props.match.params.id !== undefined) ? 'Error updating task type.': 'Error creating task type.';
                showError(errorMessage, err.message);
            },
            () =>  {
                showOperationSuccess();
                this.props.history.goBack();
            }
        );
      };

    onCancel = () => {
        this.props.history.goBack();
    }

    render() {
        const timeFormat = 'HH:mm';
        const title = this.props.match.params.id  !== undefined ? 'Edit task type' : 'Create new task type';
        const description = this.state.taskType.id > 0 ? `${this.state.taskType.name}` : '';
        if (!this.state.taskType) {
            return (<div>Loading</div>);
        }
        else if (this.state.taskType) {
            return(<div>
                <Form initialValues={{ 
                    name: this.state.taskType.name, 
                    cost: this.state.taskType.cost,
                    duration: this.state.taskType.momentDuration }} 
                    ref={this.formRef} name="control-ref" onFinish={this.onFinish}>
                    <PageHeader
                        style={{ paddingLeft : 0, paddingRight : 0}}
                        onBack={this.props.taskTypesState.loading ? undefined : this.onCancel}
                        title={title}
                        subTitle={description}
                        extra={[
                            <Button disabled={this.props.taskTypesState.loading} key='buttonSave' type="primary" htmlType="submit">Save</Button> ,
                            <Button disabled={this.props.taskTypesState.loading} key='buttonCancel' type='ghost' onClick={this.onCancel}  >Cancel</Button> 
                        ]}>  
                    </PageHeader>
                  <div>
                    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col span={24}>
                          <Form.Item
                            label="Name"
                            name="name"
                            rules={[{ required: true, message: 'Please input task type name!' }]}>
                            <Input style={{ width: '100%' }} placeholder="brief task type name"/>
                          </Form.Item>
                        </Col>
                    </Row>
                    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                        <Col span={12}>
                          <Form.Item
                            label="Cost"
                            name="cost">
                            <InputNumber  min={0} style={{ width: '100%' }}/>
                          </Form.Item>
                        </Col>
                        <Col className="gutter-row" span={12}>
                          <Form.Item
                            label="Duration"
                            name="duration">
                             <TimePicker minuteStep={5} format={timeFormat} style={{ width: '100%' }} />
                          </Form.Item>
                        </Col>
                    </Row>
                  </div>
                  </Form>
                  </div>
                  
                  );
        }
        else {
            return(<div>Erros aconteceram.</div>);
        }  
    }
}

export default withRouter(taskTypeconnector(TaskType));