export enum ActionType {
  GET_TASK_TYPES = 'get_task_types',
  UPSERT_TASK_TYPE = 'upsert_task_type',
  DELETE_TASK_TYPE = 'delete_task_type',
  SET_LOADING = 'loading_task_type',
}

export interface ITaskType {
  key: number,
  id: number,
  name: string,
  cost : number,
  duration: number
}

export type TaskTypeState = {
  taskTypes : ITaskType[],
  totalCount: number,
  pageSize : number,
  pageIndex : number,
  loading: boolean
}
      