import { ActionType, ITaskType } from "./actionTypes"
import { Dispatch } from 'redux';
import axios from 'axios';
import { globalSettings} from '../../global'

export type LoadingTaskTypeAction = {
  type: ActionType.SET_LOADING,
  payload: boolean
}

export type DeleteTaskTypeAction = {
  type: ActionType.DELETE_TASK_TYPE,
  payload: number
}

export type GetTaskTypesPayload = {
  tasks : ITaskType[],
  totalCount: number,
  pageSize : number,
  pageIndex : number
}

export type GetTaskTypesAction = {
  type: ActionType.GET_TASK_TYPES,
  payload: GetTaskTypesPayload
}

export type UpsertTaskTypeAction = {
  type: ActionType.UPSERT_TASK_TYPE,
  payload: ITaskType
}
  
export type Action =
    | UpsertTaskTypeAction
    | DeleteTaskTypeAction
    | GetTaskTypesAction
    | LoadingTaskTypeAction;

export const setLoadingState = (loading: boolean) => {
  return async (dispatch: Dispatch<Action>) => {
    dispatch({
        type: ActionType.SET_LOADING,
        payload : loading
    });
  }
}

export const deleteTaskType = (taskType: ITaskType, onError : (err: any) => void) => {
    return async (dispatch: Dispatch<Action>) => {
      dispatch({
        type: ActionType.SET_LOADING,
        payload : true
      });
      try {
        const address =`${globalSettings.ApiAdress}tasktypes/${taskType.id}`;
        const { data } = await axios.delete(address);
        console.log(data);
            dispatch({
                type: ActionType.DELETE_TASK_TYPE,
                payload: taskType.id
            });
      }
      catch(err) {
        dispatch({
          type: ActionType.SET_LOADING,
          payload : false
        });
        onError(err);
      }
    }
}

export const upsertTaskType = (taskType : ITaskType, onError : (err: any) => void, onSuccess? : () => void) => {
    return async (dispatch: Dispatch<Action>) => {
        dispatch({
            type: ActionType.SET_LOADING,
            payload : true
        });
        try {
            const address = taskType.id > 0 ? `tasktypes/${taskType.id}` : 'tasktypes' ;
            const { data } = taskType.id > 0 ? 
                await axios.put(`${globalSettings.ApiAdress}${address}`, taskType) :
                await axios.post(`${globalSettings.ApiAdress}${address}`, taskType) ;

            console.log(data);
            dispatch({
                type: ActionType.UPSERT_TASK_TYPE,
                payload: data
            });

            if (onSuccess) {
                onSuccess();
            }
        }
        catch(err) {
            dispatch({
              type: ActionType.SET_LOADING,
              payload : false
            });
            onError(err);
        }
    }
}

export const getTaskTypes = (pageIndex: number, pageSize: number) => {
  return async (dispatch: Dispatch<Action>) => {
    try {
      const { data } = await axios.get('https://tskrai-api.azurewebsites.net/tasktypes',{
        params: {
          pageSize: pageSize,
          pageIndex: pageIndex
        }
      });
      
      const payload = {
        tasks : data.items.map((taskType :ITaskType, index: number) => { 
            taskType.key = index;
            return taskType;
        }),
        totalCount: data.totalCount,
        pageIndex: data.pageIndex,
        pageSize: data.pageSize
      };
      
      dispatch({
        type: ActionType.GET_TASK_TYPES,
        payload: payload
      });

    } catch (err) {
      console.log(err);
    }    
  }
}