import { bindActionCreators } from "redux";
import { RootState } from "../../../store/reducer";
import { connect, ConnectedProps } from 'react-redux';
import { upsertTaskType, setLoadingState } from '../../../store/tasktypes/actionsCreator';
import { TaskTypeStateProps } from "./taskTypeStateProps";

const mapStateToProps = (state: RootState) : TaskTypeStateProps  => ( {
    taskTypesState : state.taskTypesReducer,
})

const mapDispatchToProps = (dispatch: any) =>
    bindActionCreators({ upsertTaskType, setLoadingState }, dispatch);

export const taskTypeconnector = connect(mapStateToProps, mapDispatchToProps);
  
export type TaskTypesProps = ConnectedProps<typeof taskTypeconnector>;