import { ActionType, ITask } from "./actionTypes"
import { Dispatch } from 'redux';
import axios from 'axios';
import { globalSettings} from '../../global'

  
  export type AddTaskAction = {
    type: ActionType.ADD_TASK
    payload: ITask
  }

  export type GetTaskPayload = {
    tasks : ITask[],
    totalCount: number,
    pageSize : number,
    pageIndex : number
  }

  export type LoadingTaskAction = {
    type: ActionType.SET_LOADING,
    payload: boolean
  }
  export type GetTasksAction = {
    type: ActionType.GET_TASKS
    payload: GetTaskPayload
  }

  export type DeleteTaskAction = {
    type: ActionType.DELETE_TASK,
    payload: number
  }
  
  
export type Action =
    | AddTaskAction
    | GetTasksAction
    | DeleteTaskAction
    | LoadingTaskAction;


export const setLoadingState = (loading: boolean) => {
    return async (dispatch: Dispatch<Action>) => {
        dispatch({
            type: ActionType.SET_LOADING,
            payload : loading
        });
    }
}

export const deleteTask = (task: ITask, onError : (err: any) => void) => {
    return async (dispatch: Dispatch<Action>) => {
        dispatch({
          type: ActionType.SET_LOADING,
          payload : true
        });
        try {
          console.log(task);
          const address =`${globalSettings.ApiAdress}tasks/${task.id}`;
          const result = await axios.delete(address);
          console.log(result);
          dispatch({
              type: ActionType.DELETE_TASK,
              payload: task.id
          });
        }
        catch(err) {
          dispatch({
            type: ActionType.SET_LOADING,
            payload : false
          });
          onError(err);
        }
    }
}

export const getTasks = (pageIndex: number, pageSize: number) => {
  return async (dispatch: Dispatch<Action>) => {
    try {
        const { data } = await axios.get(`${globalSettings.ApiAdress}tasks`,{
          params: {
            pageSize: pageSize,
            pageIndex: pageIndex
         }
        });

        const payload = {
          tasks : data.items.map((task :ITask, index: number) => { 
              task.key = index;
              return task;
            }),
          totalCount: data.totalCount,
          pageIndex: data.pageIndex,
          pageSize: data.pageSize
        };

      /*
      items: 
      "indexFrom": 0,
  "pageIndex": 1,
  "pageSize": 10,
  "totalCount": 300,
  "totalPages": 30,
  "hasPreviousPage": true,
  "hasNextPage": true*/

        
         dispatch({
          type: ActionType.GET_TASKS,
          payload: payload
        });
      } catch (err) {
        console.log(err);
      }    
  }
}

