import React from 'react';
import { ITask, TasksState } from '../../store/tasks/actionTypes';
import Table from 'antd/lib/table/Table';
import { ColumnType } from 'antd/lib/table';
import { Button, PageHeader, Space } from 'antd';
import moment from 'moment';
import {withRouter, RouteComponentProps} from 'react-router-dom';
import { taskListConnector, TaskListProps } from '../props/tasks/index';
import { showDeleteConfirm, showError } from './helpers';


class TaskList extends React.Component<TaskListProps & RouteComponentProps> {

    componentDidMount() {
        this.props.getTasks(this.props.taskState.pageIndex, this.props.taskState.pageSize);
    }

    onShowDeleteConfirm = (task: ITask) => {
      showDeleteConfirm( () => this.props.deleteTask(task, this.onError), () => console.log('delete') );
    }

    onEditTask = (task: ITask) => {
      this.props.history.push('/tasklist/task/' + task.id);
    }

    onNewTask = () => {
      this.props.history.push('/tasklist/task');
    }

    onError= (err: Error) => {
      showError('Error deleting task', err.message);
    }


    onPageChanged = (page: number, pageSize?: number | undefined) : void => {
      this.props.getTasks(page - 1, pageSize ?? this.props.taskState.pageSize);
    }

    renderView(data : TasksState ) {
      console.log(data);
      if (data && data.tasks.length > 0)
      {
        const col = this.Columns();
        return(<Table dataSource={data.tasks} columns={col} 
          sticky={{  offsetHeader:64}} 
          pagination={{ 
            showSizeChanger: true, 
            onChange : this.onPageChanged,
            current: data.pageIndex + 1,
            pageSize : data.pageSize,
            total : data.totalCount,
            showTotal : (total) => `Total ${total} items `,
            pageSizeOptions: ['10','20','30'],
            position: [ 'bottomCenter' ]}} />);
      }
      else {
        console.log('empty');
        return(<div>Empty</div>);
      }
    }
    
    render() {
      const  { taskState }   = this.props;

      console.log(taskState);

      return(
        <div>
          <PageHeader
            style={{ paddingLeft: 0, paddingRight: 0}}
            className="site-page-header"
            title="List of tasks"
             subTitle=""
            extra={[
              <Button key='buttonAdd' type="primary" onClick={this.onNewTask}>Add New</Button> 
            ]}>  
        </PageHeader>
          {this.renderView(taskState)}
        </div>
      )
    }

    readonly Columns = () : ColumnType<ITask>[] => { 
      return [
        {
          title: 'Name',
          dataIndex: 'name',
          key: 'name',
          ellipsis: true,
        },
        {
          title: 'Date',
          dataIndex: 'date',
          key: 'date',
          ellipsis: true,
          render: (text: Date) => <span>{moment(text).format('DD-MM-YYYY')}</span>,
          width: 150
        },
        {
          title: 'Due Date',
          dataIndex: 'dueDate',
          key: 'dueDate',
          render: (text: Date) => <span>{moment(text).format('DD-MM-YYYY hh:mm')}</span>,
          ellipsis: true,
          width: 200
        },
        {
          title: 'Duration',
          dataIndex: 'durationInSeconds',
          render: (text: number) => <span>{text / 60} min</span>,
          key: 'durationInSeconds',
          width: 100
        },
        {
          title: 'Notes',
          dataIndex: 'notes',
          key: 'notes',
          ellipsis: true,
        },
        {
          title: '',
          key: 'action',
          width: 180,
          render: (text, record) => (
            <Space size="middle">
                  <Button onClick={() => this.onShowDeleteConfirm(record)} type="link">Delete</Button>
                  <Button onClick={() => this.onEditTask(record)} type="link">Edit</Button>
            </Space>
          ),
        },
      ];
    }
}

export default withRouter(taskListConnector(TaskList));