import { Action } from "./actionsCreator"
import { ActionType, ITaskType, TaskTypeState} from "./actionTypes"

const initialState: TaskTypeState = {
  taskTypes : [],
  pageSize : 10,
  pageIndex : 0,
  totalCount : 0,
  loading: false,
}

const reducer = (
    state: TaskTypeState = initialState,
    action: Action
  ): TaskTypeState => {
    switch (action.type) {
      case ActionType.GET_TASK_TYPES:
        return {...state, 
          loading: false,
          taskTypes: action.payload.tasks, 
          pageIndex : action.payload.pageIndex,
          pageSize : action.payload.pageSize,
          totalCount : action.payload.totalCount };
      case ActionType.SET_LOADING:
        return {...state, loading: action.payload };
      case ActionType.DELETE_TASK_TYPE:
        return {...state, loading: false, taskTypes: state.taskTypes.filter((tasktype: ITaskType) => tasktype.id !== action.payload) };
      case ActionType.UPSERT_TASK_TYPE:
        let found: boolean = false;
        const taskTypes = state.taskTypes.map((taskType)=> {
          if( taskType.id === action.payload.id ) {
            found = true;
            return action.payload
          } else {
            return taskType;
          }
        });
        return {...state, loading: false, taskTypes: found ? taskTypes : state.taskTypes.concat(action.payload)};
      default:
        return state;
    }
  };
  
export default reducer;
