import { Action, AddTaskAction, GetTasksAction } from './actionsCreator'
import { ITask, TasksState } from './actionTypes'
import { ActionType} from "./actionTypes"

const initialState: TasksState = {
  tasks : [],
  pageSize : 10,
  pageIndex : 0,
  totalCount : 0,
  loading: false
}

const reducer = (
    state: TasksState = initialState,
    action: Action
  ): TasksState => {
    switch (action.type) {
      case ActionType.GET_TASKS:
        return {...state, 
          tasks: action.payload.tasks, 
          pageIndex : action.payload.pageIndex,
          pageSize : action.payload.pageSize,
          totalCount : action.payload.totalCount ,
          loading : false };
      case ActionType.ADD_TASK:
        return state;
      case ActionType.DELETE_TASK: 
        return {...state, loading: false, tasks: state.tasks.filter((task: ITask) => task.id !== action.payload) };
      case ActionType.SET_LOADING:
          return {...state, loading: action.payload };
      default:
        return state;
    }
  };
  
  export default reducer;