import { Button, Col, DatePicker, Divider, Form, FormInstance, Input, Modal, PageHeader, Row, Select, TimePicker } from 'antd';
import TextArea from 'antd/lib/input/TextArea';
import axios from 'axios';
import React, { ChangeEvent } from 'react';
import { withRouter } from 'react-router-dom';
import { ITask } from '../../store/tasks/actionTypes';
import MapView from '../map/MapView';
import { ITaskModel, TaskRouteComponentProps } from '../props/tasks/index';
import debounce from 'lodash/debounce';
import { ILocation } from '../../store/locations/location';
import { toNumber } from 'lodash';
import { showError, showOperationSuccess } from './helpers';
import { globalSettings } from '../../global';
import moment from 'moment';

interface ITaskState {
  task: ITaskModel,
  showModalLocation: boolean;
  locationOptions: ILocationValueType[],
  fullLocationOptions: ILocation[],
  newLocation: ILocation | undefined,
  selectedLocation: ILocationValueType | undefined,
  userDefinedLocations: ILocation[],
  floor: string,
  userAlias: string,
}

interface ILocationValueType {
  label: string;
  value: string;
  key: string;
}

const initialTaskModel : ITaskModel = {
  location: undefined,
  date: new Date(),
  dueDate: new Date(),
  notes: undefined,
  name:'',
  duration :600,
  id: -1,
  key : -1,
  typeId: 1,
  momentDuration : moment({minutes : 10}),
  momentDate : moment(),
  momentDueDate : moment()
};

class NewTask extends React.Component<TaskRouteComponentProps, ITaskState> {
  formRef = React.createRef<FormInstance>();

  constructor(props: TaskRouteComponentProps) {
    super(props);

    this.state = {
      newLocation: undefined,
      locationOptions: [],
      fullLocationOptions: [],
      selectedLocation: undefined,
      showModalLocation: false,
      userDefinedLocations: [],
      floor: '',
      userAlias: '',
      task: initialTaskModel
    };
  }

  componentDidMount() {
    if(this.props.match.params.id !== undefined) {
      this.getTask(this.props.match.params.id);
  }
  }

  // On edit mode get the task type
  async getTask(id: string)  {
    //this.props.setLoadingState(true);
    
    axios.get(`${globalSettings.ApiAdress}tasks/${id}`)
    .then(({data}) =>  {
        //this.props.setLoadingState(false);
        this.onTaskFetched(data as ITask);
    })
    .catch((err: Error) => {
        //this.props.setLoadingState(false);
        showError('Error loading task.', err.message)
    });
  }
  
  onTaskFetched (data:ITask) {

    let date: Date = new Date(data.duration * 1000);
    const momentDuration = moment(date).utc();
    const momentDueDate = moment(data.dueDate).utc();
    const momentDate = moment(data.date).utc();

    if (data.location === undefined)
    {
      this.setState({
        task :  {...data, momentDuration, momentDate, momentDueDate } 
      });
      return;
    }

    var location = {
      value: data.location.id.toString(),
      label: data.location.alias,
      key: data.location.id.toString()
    };

    console.log(location);
    const { locationOptions } = this.state;

    this.setState({
      locationOptions: [...locationOptions, location],
      selectedLocation: location,
      task :  {...data, momentDuration, momentDate, momentDueDate } 
    });

    
    //must be done because of initial values of form that aren't updated.
    this.formRef.current?.resetFields();
}

  //debounce search 0.6 seconds to allow the user to input the full search string
  deboucedSearch =
    debounce((location) => this.onFetchLocation(location), 600);

  //fetch locations
  onFetchLocation = (location: string) => {
    if (!location || location.trim() === "") {
      return;
    }

    this.setState({
      fullLocationOptions: [],
      locationOptions: []
    });

    axios.get(`https://tskrai-api.azurewebsites.net/locations?alias=${location}`)
      .then(({ data }) => {
        const locations: ILocation[] = data.items;

        const locationOptions: ILocationValueType[] = locations.map((location: ILocation) => {
          return { key: location.id.toString(), label: location.alias, value: location.id.toString() }
        });

        //add locations that user defined
        if (this.state.userDefinedLocations) {
          this.state.userDefinedLocations.forEach(location => locationOptions.push(
            { key: location.id.toString(), label: location.alias, value: location.id.toString() }
          ))
        }

        this.setState({
          locationOptions: locationOptions,
          fullLocationOptions: locations
        });

        console.log('fetched');
      })
      .catch(function (error) {
        console.log(`onFetchLocationcoco : ${error}`);
      });
  }


  showSearchLocation = () => {
    this.setState({ showModalLocation: true });
  }

  createIdForLocation = (): number => {
    console.log(`length : ${this.state.userDefinedLocations.length}`);
    if (this.state.userDefinedLocations.length < 1)
      return -1;

    const result = Math.min.apply(null, this.state.userDefinedLocations.map((loc) => loc.id)) - 1;
    console.log(`result : ${result}`);
    return result
  }
  closeSearchLocation = () => {
    this.setState({ showModalLocation: false });

    if (!this.state.newLocation) {
      return;
    }

    const { newLocation } = this.state;
    newLocation.id = this.createIdForLocation();
    newLocation.floor = this.state.floor;
    newLocation.alias = this.state.userAlias;
    newLocation.userAlias = this.state.userAlias;

    this.setState({ newLocation: newLocation });

    var location = {
      value: this.state.newLocation.id.toString(),
      label: this.state.newLocation.alias,
      key: this.state.newLocation.id.toString()
    };

    const { userDefinedLocations, locationOptions } = this.state;

    this.setState({
      userDefinedLocations: [...userDefinedLocations, this.state.newLocation],
      locationOptions: [...locationOptions, location],
      selectedLocation: location
    });

  }


  onLocationSelected = (location: ILocation) => {
    this.setState({ newLocation: location })
  }

  onCancel = () => {
    this.props.history.goBack();//push('/tasklist/new')
  }


  onChangeLocationFloor = (floor: ChangeEvent<HTMLInputElement>) => {
    this.setState({ floor: floor.target.value })
  }

  onChangeLocationUserAlias = (alias: ChangeEvent<HTMLInputElement>) => {
    this.setState({ userAlias: alias.target.value })
  }

  findLocation = (locationValueType: ILocationValueType | undefined): ILocation | undefined => {
    if (locationValueType === undefined) {
      return undefined;
    }

    const id = toNumber(locationValueType.value);
    let location = this.state.fullLocationOptions.find(loc => loc.id === id);
    if (location === undefined) {
      location = this.state.userDefinedLocations.find(loc => loc.id === id);
    }
    return location;
  }

  onFinish = (values: any) => {
    console.log(values);

    const task: ITask = {
      id: -1,
      date: values.date.toDate(),
      dueDate: values.dueDate.toDate(),
      duration: (values.duration.hours() * 60) + values.duration.minutes(),
      key: -1,
      location: this.findLocation(this.state.selectedLocation),
      name: values.name,
      notes: values.notes,
      typeId: values.typeId
    }
    console.log(task);
    axios.post('https://tskrai-api.azurewebsites.net/tasks', task)
      .then((response) => {
        showOperationSuccess();
        this.props.history.goBack();
      })
      .catch((error) => {
        const errorMessage = (this.props.match.params.id !== undefined) ? 'Error updating task.' : 'Error creating task.';
        showError(errorMessage, error.message);
      });
  };



  render() {
    const options = [{ label: 'Cleaning', value: 1 },
    { label: 'CheckIn', value: 2 },
    { label: 'Delivery', value: 3 }]
    const timeFormat = 'HH:mm';
    const dateFormat = 'yy-MM-DD HH:mm';
    const layout = {
      labelCol: { span: 6 },
      wrapperCol: { span: 18 },
    };
    const freeText = { span: 3 };
    const title = this.props.match.params.id  !== undefined ? 'Edit task' : 'Create new task';
    const description = this.state.task.id > 0 ? `${this.state.task.name}` : '';
    const { locationOptions, selectedLocation } = this.state;
    return (<div>
      <Form {...layout} ref={this.formRef} name="control-ref" onFinish={this.onFinish}
        initialValues={{ 
          name: this.state.task.name, 
          typeId: this.state.task.typeId,
          duration: this.state.task.momentDuration,
          date: this.state.task.momentDate,
          dueDate: this.state.task.momentDueDate,
          notes : this.state.task.notes,
          location: selectedLocation  }} >
        <PageHeader

          onBack={this.onCancel}
          title={title}
          subTitle={description}
          extra={[
            <Button key='buttonSave' type="primary" htmlType="submit">Save</Button>,
            <Button key='buttonCancel' type='ghost' onClick={this.onCancel}  >Cancel</Button>
          ]}>
        </PageHeader>
        <div>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={12}>
              <Form.Item label="Name"name="name" rules={[{ required: true, message: 'Please input task name!' }]}>
                <Input />
              </Form.Item>
            </Col>
            <Col className="gutter-row" span={12}>
              <Form.Item label="Task type" name="typeId" rules={[{ required: true, message: 'Please select a task type' }]}>
                <Select
                  placeholder="Select a type"
                  options={options}
                  allowClear>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={12}>
              <Form.Item label="Date" name="date" rules={[{ required: true, message: 'Please select a date to execute the task.' }]}>
                <DatePicker showTime style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col className="gutter-row" span={12}>
              <Form.Item
                label="Location"
                name="location"
                rules={[{ required: true, message: 'Please select the location of the task.' }]}>
                <Select<ILocationValueType>
                  labelInValue

                  showSearch
                  onSearch={this.deboucedSearch}
                  placeholder="Search for a location"
                  showArrow={false}
                  value={this.state.selectedLocation}
                  onChange={newValue => {
                    console.log(newValue);
                    this.setState({ selectedLocation: newValue });
                  }}
                 
                  allowClear
                  options={locationOptions}
                  dropdownRender={menu => (
                    <div>
                      {menu}
                      <Divider style={{ margin: '4px 0' }} />
                      <div style={{ display: 'flex', flexWrap: 'nowrap', padding: 2 }}>
                        <Button type="link" onClick={this.showSearchLocation}>Add item</Button>

                      </div>
                    </div>
                  )}>
                </Select>

              </Form.Item>
            </Col>
          </Row>
          <Modal
            title="Enter Address to find the location"
            centered
            visible={this.state.showModalLocation}
            onOk={this.closeSearchLocation}
            onCancel={this.closeSearchLocation}
            width={800}
          >
            <MapView onLocationSelected={this.onLocationSelected}></MapView>
            <Row>
              <Col span={24}>
                <div style={{ margin: '10px 0' }}>
                  <span>Address:</span>
                  <Input value={this.state.newLocation?.alias} readOnly />
                </div>
              </Col>
            </Row>
            <Row>
              <Col span={16}>
                <div style={{ margin: '5px 0' }}>
                  <span>Alias:</span>
                  <Input value={this.state.newLocation?.userAlias} onChange={this.onChangeLocationUserAlias} />
                </div>
              </Col>
              <Col span={2}>

              </Col>
              <Col span={6}>
                <div style={{ margin: '5px 0' }}>
                  <span>Floor:</span>
                  <Input value={this.state.newLocation?.floor} onChange={this.onChangeLocationFloor} />
                </div>
              </Col>
            </Row>
          </Modal>
          <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
            <Col span={12}>
              <Form.Item label="Due Date"name="dueDate">
                <DatePicker format={dateFormat} showTime style={{ width: '100%' }} />
              </Form.Item>
            </Col>
            <Col className="gutter-row" span={12}>
              <Form.Item label="Duration" name="duration">
                <TimePicker minuteStep={5} format={timeFormat} style={{ width: '100%' }} />
              </Form.Item>
            </Col>
          </Row>
          <Row>
            <Col className="gutter-row" span={24}>
              <Form.Item
                labelCol={freeText}
                wrapperCol={{ span: 21 }}
                label="Notes"
                name="notes">
                <TextArea
                  style={{ width: '100%' }}
                  placeholder="Additional information"
                  autoSize={{ minRows: 3, maxRows: 5 }}
                />
              </Form.Item>
            </Col>
          </Row>
        </div>
      </Form>
    </div>);
  }
}

export default withRouter(NewTask);