import { ILocation } from "../locations/location";

export enum ActionType {
  ADD_TASK = 'add_task',
  GET_TASKS = 'get_tasks',
  DELETE_TASK = 'delete_task',
  SET_LOADING = 'loading_task',
}

export interface ITask {
  id:number,
  key: number,
  name: string
  typeId: number
  location: ILocation | undefined,
  date: Date
  dueDate: Date
  duration: number
  notes: string | undefined
}

export type TasksState = {
  tasks : ITask[],
  totalCount: number,
  pageSize : number,
  pageIndex : number,
  loading: boolean
}
  