import Table, { ColumnType } from 'antd/lib/table';
import React from 'react';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { ITaskType } from '../../store/tasktypes/actionTypes';
import { Button, PageHeader, Space } from 'antd';
import { showDeleteConfirm, showError } from './helpers';
import { TaskTypeListProps, taskTypesListConnector } from '../props/tasktypes/index';

class TaskTypeList extends React.Component<TaskTypeListProps & RouteComponentProps> {

    componentDidMount() {
      this.props.getTaskTypes(this.props.taskTypesState.pageIndex, this.props.taskTypesState.pageSize);    
    }

    onShowDeleteConfirm = (tasktype: ITaskType) => {
      showDeleteConfirm( () => this.props.deleteTaskType(tasktype, this.onError), () => console.log('delete') );
    }

    onPageChanged = (page: number, pageSize?: number | undefined) : void => {
      this.props.getTaskTypes(page - 1, pageSize ?? this.props.taskTypesState.pageSize);
    }

    onEditTaskType = (tasktype: ITaskType) => {
        this.props.history.push('/tasktypelist/tasktype/' + tasktype.id);
    }

    onNewTaskType = () => {
        this.props.history.push('/tasktypelist/tasktype');
    }

    onError= (err: Error) => {
      showError('Error deleting task type', err.message);
    }

    
    render() {
        const  { taskTypesState }   = this.props;

        if (taskTypesState && taskTypesState.taskTypes.length > 0)
        {
          const col = this.Columns();
          return(
            <div>
              <PageHeader
                style={{ paddingLeft: 0, paddingRight: 0}}
                className="site-page-header"
                title="List of task types"
                subTitle=""
                extra={[
                  <Button key='buttonAdd' type="primary" onClick={this.onNewTaskType}>Add New</Button> 
                ]}>  
              </PageHeader>
              <Table dataSource={taskTypesState.taskTypes} columns={col} 
                pagination={{ 
                showSizeChanger: true, 
                onChange : this.onPageChanged,
                current: taskTypesState.pageIndex + 1,
                pageSize : taskTypesState.pageSize,
                showTotal : (total) => `Total ${total} items `,
                total : taskTypesState.totalCount,
                pageSizeOptions: ['10','20','30'],
                position: [ 'bottomCenter' ]}} />
            </div>
          );
        }
        else {
          return(<div>Empty</div>);
        };
    }

    readonly Columns = () : ColumnType<ITaskType>[] => { 
        return [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            ellipsis: true,
        },
        {
            title: 'Cost',
            dataIndex: 'cost',
            key: 'date',
            width: 150
        },
        {
            title: 'Duration',
            dataIndex: 'duration',
            render: (text: number) => <span>{text / 60} min</span>,
            key: 'duration',
            width: 100
        },
        {
            title: '',
            key: 'action',
            width: 180,
            render: (text, record) => (
            <Space size="middle">
                <Button onClick={() => this.onShowDeleteConfirm(record)} type="link">Delete</Button>
                <Button onClick={() => this.onEditTaskType(record)} type="link">Edit</Button>
            </Space>
            ),
        },
        ];
    }
}

export default withRouter(taskTypesListConnector(TaskTypeList));